import { AfterViewInit, Component, ComponentFactoryResolver, ComponentRef, ElementRef, Input, OnChanges, OnDestroy, OnInit, SimpleChanges, ViewChild, ViewContainerRef } from "@angular/core";
import { AssessmentResult } from "app/shared/models/assessment-result";
import { IReport, QuestionnaireAssessmentMapping } from "../models/i-report";
import { NavBarService } from "app/theme/services/nav-bar.service";
import { Assessment } from "app/shared/models/assessment";
import { ScaleResult } from "app/shared/models/scale-result";
import { ResultComputation } from "../models/result-computation";
import { HtmlContentItemComponent } from "../report-items/html-content-item/html-content-item.component";
import { ScaleResultsSectionsListComponent } from "../report-items/scale-results-sections-list/scale-results-sections-list.component";
import { AddoScaleResultsComponent } from "../report-items/addo-scale-results/addo-scale-results.component";
import { ComputedScaleComponent } from "../report-items/computed-scale/computed-scale.component";
import { ScaleResultsSummaryComponent } from "../report-items/scale-results-summary/scale-results-summary.component";
import { TherapistScaleResultsComponent } from "../report-items/therapist-scale-results/therapist-scale-results.component";
import { FlaggedQuestionsListComponent } from "../report-items/flagged-questions-list/flagged-questions-list.component";
import { FlaggedQuestionsListSimpleComponent } from "../report-items/flagged-questions-list-simple/flagged-questions-list-simple.component";
import { PclAssessmentResultsComponent } from "../report-items/pcl-assessment-results/pcl-assessment-results.component";
import { AssessmentQuestionsListComponent } from "../report-items/assessment-questions-list/assessment-questions-list.component";
import { QuestionActionListComponent } from "../report-items/question-action-list/question-action-list.component";
import { ClientScaleResultsComponent } from "../report-items/client-scale-results/client-scale-results.component";
import { TextItemComponent } from "../text-item/text-item.component";
import { IReportItemComponent } from "../models/i-report-item-component";
import { UniqueIdService } from "app/shared/services/unique-id.service";
import { SsoCrossDomainMessenger } from "app/shared/services/sso-cross-domain-messenger";
import { AssessmentHydrator } from "app/shared/services/assessment-hydrator.service";
import { DacConfig } from "app/shared/services/dac-config.service";
import {Variable} from "../models/variable";
import { NavBarItem } from "app/theme/models/nav-bar-item";

