import { Injectable } from "@angular/core";
import {User} from "../models/user";
import {Role} from "../models/role";
import {HTTPService} from "./http.service";
import { PaginatedResultResponse } from "../models/paginated-results-response";
import { PaginatedResultsLoaderFactory } from "./pagination-results-loader-factory.service";
import { PaginatedResultsLoader } from "../models/paginated-results-loader";

@Injectable()
export class UserService {
    private users:User[];
    private _resultsLoader:PaginatedResultsLoader<User>;

    constructor(private dac$http:HTTPService, private _paginationFactory:PaginatedResultsLoaderFactory) {
        //  let admin = new User();
        // admin.username = "admin";
        // admin.id = 1;
        // admin.role = Role.Admin;

        // let user = new User();
        // user.username = "snielson";
        // user.id = 2;

        // let user2 = new User();
        // user2.username = "kskinner";
        // user2.id = 3;
        // this.users = [admin, user, user2];
    }

    getUserById(id:string) : Promise<User> {
      return this.dac$http.get<User[]>("assessment-users/" + id).then((response) => {
          let user = this.mapUser(response);
          return user;
      });
    }

    getUsers(offset?:number) : Promise<User[]> {
      // TODO: @adunsulag need to support more than 50 users
        var data: any = {
          _offset: offset || 0
          , _limit: 50
        };
        return this.dac$http.get<PaginatedResultResponse<User>>("assessment-users/", data).then((data) => {
          if (data && data.results.length) {
            return data.results.map(u => this.mapUser(u));
          }
        });
    }

    createUser(userToCreate:User) {
        return this.dac$http.post<User>("assessment-users/", userToCreate).then((response) => {
            return this.mapUser(response);
        });
    }

    saveUser(user:User) {
        return this.dac$http.post("assessment-users/" + user.id + ".json", user).then((response) => {
            return user;
        });
    }

    getUser(id:number) {
        return this.getUsers().then((users) => {
            let user = users.find(user => user.id == id);
            let newUser = new User();
            let copied = Object.assign(newUser, user);
            return copied;
        });
    }

    getUserByStringId(id:string) {
      return this.getUsers().then((users) => {
          let user = users.find(user => (user.id + "") == id);
          if (!user) {
            throw new Error("Failed to find user with id " + id);
          }
          let newUser = new User();
          let copied = Object.assign(newUser, user);
          return copied;
      });
  }

    isDuplicateUsername(username:string) : Promise<boolean> {
        // need to make sure we handle special characters
        return this.dac$http.post("users/check-username", {_username: username})
            .then((response) => {
                if (response) {
                    return true;
                }
        }).catch((error) => {
            if (error.status === 404) {
                return false;
            }
            throw error;
        });
    }

    restoreUser(userToRestore:User) {
        let user = new User();
        Object.assign(user, userToRestore);
        user.enabled = true;
        return this.saveUser(user).then(savedUser => {
            userToRestore.enabled = true;
            return userToRestore;
        });
    }

    archiveUser(userToArchive:User) {
        let user = new User();
        Object.assign(user, userToArchive);
        user.enabled = false;
        return this.saveUser(user).then(savedUser => {
            userToArchive.enabled = false;
            return userToArchive;
        });
    }

    // we removed the API endpoint to delete users
    // TODO: stephen if we ever enable user deletion again add this back in.
    // deleteUser(userToDelete:User, userToReassignTo:User) {
    //     return this.dac$http.delete("users/" + userToDelete.id + ".json", {reassignUserId: userToReassignTo.id});
    // }

    private mapUser(u:any) {
        let user = new User();
        Object.assign(user, u);
        return user;
    }
}
