import { Injectable } from "@angular/core";
import { BehaviorSubject, Observable, Subject } from "rxjs";
import {Auth,CognitoUser} from '@aws-amplify/auth';
import { AuthState } from "../models/AuthState";
import { CognitoUserInterface } from "../models/CognitoUserInterface";
import { AuthStateEvent } from "../models/AuthStateEvent";
import { SignupParams } from "../models/SignupParams";

@Injectable({
  providedIn: 'root',
})
export class AmplifyAuthService
{
  private readonly INITIAL_STATE:AuthState = AuthState.SIGN_IN;
  private currentUser:CognitoUserInterface;
  private currentState:AuthState;
  private _stateChange:Subject<AuthStateEvent>;
  constructor() {
    this._stateChange = new BehaviorSubject<AuthStateEvent>({
      state: this.INITIAL_STATE
      ,user: null
    });
  }

  public setAuthState(change:AuthStateEvent) {
    this.currentState = change.state;
    this.currentUser = change.user;
    this._stateChange.next({state: this.currentState, user: this.currentUser});
  }

  /**
   * Resets the auth state back to the initial state of the service.
   */
  public resetAuthState() {
    this.setAuthState({state: this.INITIAL_STATE, user: null});
  }

  public authStateChange$() : Observable<AuthStateEvent> {
    return this._stateChange;
  }

  public async resendSignUp(email: string) {
    return Auth.resendSignUp(email);
  }
  public async signUp(user:SignupParams) {
    return Auth.signUp(user);
  }

  public async confirmSignUp(email: string, code: string) {
    return Auth.confirmSignUp(email, code);
  }

  public async confirmSignIn(user:CognitoUserInterface, mfaCode:string, mfaType:"SMS_MFA"|"SOFTWARE_TOKEN_MFA"|null) {
    // we only support SMS_MFA for now
    return Auth.confirmSignIn(user, mfaCode, mfaType);
  }

  public async forgotPassword(username:string) {
    return Auth.forgotPassword(username);
  }

  public async forgotPasswordSubmit(username:string, forgotPasswordCode:string, newPassword:string) {
    return Auth.forgotPasswordSubmit(username, forgotPasswordCode, newPassword);
  }

  public async completeNewPassword(user:CognitoUserInterface, newPassword:string, requiredAttributes:any) {
    return Auth.completeNewPassword(user, newPassword, requiredAttributes)
  }

  public async signIn(username, password) {
    return Auth.signIn(username, password);
  }

  public async signOut(settings?:{ global: boolean }) {
    let result = await Auth.signOut(settings);
    this.setAuthState({state:this.INITIAL_STATE});
    return result;
  }
}
