import { Injectable } from "@angular/core";
import { HTTPService } from "../../shared/services/http.service";
import { ManagedClient } from "../../shared/models/managed-client";
import { PaginatedResultResponse } from "../../shared/models/paginated-results-response";
import { User } from "app/shared/models/user";
import { BehaviorSubject ,  Observable } from "rxjs";
import { AssignmentHydratorService } from "./assignment-hydrator.service";
import { TaskService } from "./task-service";
import { FhirService } from "./fhir-service";

@Injectable()
export class ClientService {

	private _dac$http: HTTPService;
	private _clientNotice: BehaviorSubject<ManagedClient>;

	constructor(dac$http: HTTPService,
		private assignmentHydrator:AssignmentHydratorService,
    private fhirService:FhirService,
    private taskService:TaskService) {
		this._dac$http = dac$http;
		this._clientNotice = new BehaviorSubject<ManagedClient>(null);
	}

	generateClientId() : Promise<string> {
		return this._dac$http.post<{id:string}>("utils/generateClientId", {}).then((serverResponse) => {
			return serverResponse.id;
		});
	}

	getClientObservable(): Observable<ManagedClient> {
		return this._clientNotice;
	}

	getClientList(user?: User, offset?: number): Promise<PaginatedResultResponse<ManagedClient>> {
		var data: any = {
			_limit: 10,
      _sort: "-date,lname,fname"
		};
		if (offset) {
			data._offset = offset; // this.encodeCursor(cursor);
		}
		if (user) {
			data.user = user.id;
		}
		return this._dac$http.get<PaginatedResultResponse<ManagedClient>>("reports/clients", data)
			.then((data) => {
				return Promise.all(data.results.map((record) => this.hydrateClient(record))).then(results => {
          data.results = results;
          return data;
        });
			});
	}

	sendClientEmail(clientId:string, subj:string, message:string, isTest:boolean) {
		var data = {
			subject:subj
			,message:message
			,isTest: (isTest ? 1 : 0)
		};
		return this._dac$http.post("clients/" + clientId + "/messages", data)
		.then((serverResponse) => {
			return {};
		});
	}

	createClient(client: ManagedClient) {
		return this._dac$http.post<ManagedClient>("clients/", client)
			.then((serverResponse) => {
				this.hydrateClient(client)
        .then(client => {
          let newClient = client;
          this._clientNotice.next(newClient);
          return newClient;
        })
			})
			.catch((response) => {
				console.error(response);
				if (response.status === 400) {
					let data = response.body as any;
					if (data) {
						throw data;
					}
				}
				else {
					throw response;
				}
			});
	}

	/**
	 * Any in memory modifications of the client, or client properties need to be notified
	 * to other services and this method handles that.
	 * @param client
	 */
	syncLocalClient(client:ManagedClient) {
		this._clientNotice.next(client);
	}

	saveClient(client: ManagedClient) {
		return this._dac$http.post<ManagedClient>("clients/" + client.id, client)
			.then((data) => {
				this._clientNotice.next(client);
				return data;
			});
	}

	async getClient(clientId: string): Promise<ManagedClient> {
    let client = await this.fhirService.getFhirClient();
    return client.request("Patient/" + clientId)
		// return this._dac$http.get<ManagedClient>("Patient/" + clientId)
			.then((serverResponse) => {
				return this.hydrateFhirPatient(serverResponse);
			});
	}

	searchClientList(searchQueryObj: {firstName?:string, lastName?:string, id?:string}, user?: User, offset?: number) {
		let search:any = searchQueryObj || {};
		search._limit = 10;
    search._sort = "-date,lname,fname";
		if (offset) {
      // need to make sure uri components such as + sign are encoded properly
			search._offset = offset; //this.encodeCursor(cursor);
		}
		if (user) {
			search.user = user.id;
		}
		return this._dac$http.get<PaginatedResultResponse<ManagedClient>>("reports/clients", search)
			.then((data) => {
				return Promise.all(data.results.map((record) => this.hydrateClient(record)))
        .then(results => {
          data.results = results;
          return data;
        });
			});
	}

  private encodeCursor(cursor)
  {
    // need to make sure uri components such as + sign are encoded properly
    return cursor;
    // return encodeURIComponent(cursor);
  }

  private hydrateFhirPatient(record) : Promise<ManagedClient> {
    let user = new User();
		let client = new ManagedClient();
    client.id = record.id;
    if (record.name) {
      let n = record.name.find(n => n.use == 'official');
      if (n) {
        client.firstName = n.given.length ? n.given.join(" ") : "";
        client.lastName = n.family || "";
        client.displayName = n.text || (client.firstName + " " + client.lastName).trim();
      }
    }
    if (record.generalPractitioner && record.generalPractitioner.length) {
      // we only grab the first one
      let provider = record.generalPractitioner[0];
      let providerParts = provider.reference.split("/");
      client.user = new User();
      client.user.username = providerParts[1] || null;
    }
    if (record.telecom && record.telecom.length) {
      let emailContacts = record.telecom.filter(t => t.system == 'email');
      if (emailContacts) {
        let workContact = emailContacts.find(e => e.use == 'work');
        let homeContact = emailContacts.find(e => e.use == 'home');
        client.email = workContact || homeContact; // order of preference
      }
    }
    return this.taskService.getAssignmentTasksForClient(client.id)
    .then(assignments => {
      client.assignments = assignments;
      return client;
    });
  }

	private hydrateClient(record): Promise<ManagedClient> {
		let user = new User();
		let client = new ManagedClient();
		Object.assign(client, record);
		if (record.assignedUser) {
			client.user = Object.assign(user, record.assignedUser);
		}
    return this.taskService.getAssignmentTasksForClient(client.id)
    .then(assignments => {
      client.assignments = assignments;
      return client;
    });
		// if (client.assignments) {
		// 	client.assignments = record.assignments.map(a => this.assignmentHydrator.hydrateAssignment(a));
		// }
		// return client;
	}
}
