import {PaginationQueryFunc} from "./pagination-query-func";
import {PaginatedResultResponse} from "./paginated-results-response";

export class PaginatedResultsLoader<T> {

    private _results:T[];
    private _offset:number;
    private _hasMoreResults:boolean;
    private _clientId:string;
    private _queryFunc:PaginationQueryFunc;

    constructor(queryFunc:PaginationQueryFunc) {
        this._results = [];
        this._offset = 0;
        this._hasMoreResults = false;
        this._queryFunc = queryFunc;
    }

    public get offset() : number {
        return this._offset;
    }

    public set offset(v:number) {
        this._offset = v;
    }

    public hasMoreResults() {
        return this._hasMoreResults;
    }

    public getResults() : Promise<T[]> {
        return this._queryFunc(this.offset)
        .then((resultList:PaginatedResultResponse<T>) => this.handleResultsLoaded(resultList));
    }

    private handleResultsLoaded(resultList:PaginatedResultResponse<T>) : T[] {
        this._results = this._results.concat(resultList.results);
        this._offset = 0;
        this._hasMoreResults = false;

        if (typeof resultList._offset === "number") {
            this.offset = resultList._offset;
            this._hasMoreResults = resultList.hasMoreData;
        }

        return this._results;
    }

    public loadMore() : Promise<T[]> {
        return this.getResults();
    }

}
