import {ScaleResult} from "./scale-result";
import {Assessment} from "./assessment";
import {QuestionSet} from "./question-set";
import {Question} from "./question";
import { AssessmentResultAnswer } from "./assessment-result-answer";

export class AssessmentResult {
	private _id:string;
	private _assignmentItemId:number;
	private _assessment:Assessment;
	private _scaleResults:ScaleResult[];
	private _flaggedQuestions:Question[];
  private _answers: AssessmentResultAnswer[];
  private _answersByQuestionId:Map<string, AssessmentResultAnswer>;
  private _dateCompleted:Date;

	constructor() {
		this._scaleResults = [];
    this._answers = [];
    this._answersByQuestionId = new Map<string, AssessmentResultAnswer>();
    this._dateCompleted = new Date();
	}


  public get dateCompleted() : Date {
    return this._dateCompleted;
  }

  public set dateCompleted(v : Date) {
    this._dateCompleted = v;
  }

	public get assignmentItemId() : number {
		return this._assignmentItemId;
	}

	public set assignmentItemId(v:number) {
		this._assignmentItemId = v;
	}
	public get id() :string {
		return this._id;
	}

	public set id(v:string) {
		this._id = v;
	}

	public get assessment() : Assessment {
		return this._assessment;
	}

	public set assessment(v : Assessment) {
		this._assessment = v;
	}


	public get scaleResults() : ScaleResult[] {
		return this._scaleResults;
	}

	public set scaleResults(v : ScaleResult[]) {
		this._scaleResults = v;
	}

	public get hasResources() : boolean {
		return this.assessment.resourcesURL && this.assessment.resourcesURL != "";
	}

	public get hasHomework() : boolean {
		return this.scaleResults.some(this.scaleHasHomework);
	}

	public getScalesWithHomework() : ScaleResult[] {
		return this.scaleResults.filter(this.scaleHasHomework);
	}

	public get flaggedQuestions(): Question[] {
		return this._flaggedQuestions;
	}

	public set flaggedQuestions(value: Question[]) {
		this._flaggedQuestions = value;
	}

  public get answers(): AssessmentResultAnswer[] {
    return this._answers;
  }


  public set answers(v : AssessmentResultAnswer[]) {
    this._answers = v;
    this._answersByQuestionId.clear();
    this._answers.forEach(a => this._answersByQuestionId.set(a.question_id, a));
  }

  public addAnswer(answer:AssessmentResultAnswer) {
    this._answers.push(answer);
    this._answersByQuestionId.set(answer.question_id, answer);
  }

  public getAnswerForQuestionId(questionId:string) : AssessmentResultAnswer|null {
    if (this._answersByQuestionId.has(questionId)) {
      return this._answersByQuestionId.get(questionId);
    }
    return null;
  }

	public getScaleResultByScaleId(scaleId:string) :  ScaleResult {
		// TODO: stephen optimize this later.
		let results:ScaleResult[] = this.scaleResults.filter(s => {
			return s.scale != null && s.scale.id === scaleId;
		});
		if (results.length) {
			return results[0];
		}

		return null;
	}

	private scaleHasHomework(scaleResult:ScaleResult) : boolean {
		return scaleResult.range &&
			scaleResult.range.assignmentURL != null &&
			scaleResult.range.assignmentURL != null &&
			scaleResult.range.assignmentURL != "";
	}
}
