import { AssignmentItem } from "./AssignmentItem";

export class ExternalAssignedQuestionnaire extends AssignmentItem
{
    private questionnaireReference:string|null;
    private questionnaireResponseReference:string|null;
    private questionnaireId:string;
    private task?:any;

    public constructor() {
        super();
        this.Type = "ExternalQuestionnaire";
    }

    public get QuestionnaireId() {
      return this.questionnaireId;
    }

    public get QuestionnaireReference() {
        return this.questionnaireReference;
    }

    public set QuestionnaireReference(v:string) {
        this.questionnaireReference = v;
        let parts = this.questionnaireReference.split("/");
        let id = parts[parts.length - 1];
        if (id) {
          this.questionnaireId = id;
        } else {
          this.questionnaireId = null;
        }
    }

    public get QuestionnaireResponseReference() {
      return this.questionnaireResponseReference;
    }

    public set QuestionnaireResponseReference(v:string) {
        this.questionnaireResponseReference = v;
    }

    public get Task() {
      return this.task;
    }

    public set Task(v:any) {
      this.task = v;
    }
}
