@Component({
  selector: 'dac-fhir-client',
  templateUrl: './fhir-client.component.html'
})
export class FhirClientComponent implements OnInit {

  constructor(private fhirService:FhirService, private dacConfig:DacConfig) { }

  ngOnInit() {
    if (this.dacConfig.isFeatureEnabled("debug")) {
      console.log("debug is enabled");
    } else {
      console.log("debug is disabled");
    }
  }

  public refreshToken() {

    this.fhirService.refreshToken().then(result => {
      console.log("Refresh token occurred");
    })
    .catch(error => {
      console.error(error);
    })
  }

}
