import { Location } from '@angular/common';
import { Component, OnInit, AfterViewInit, ElementRef, Input, ViewChild } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';

import { AssessmentService } from "../../shared/services/assessment.service";
import { AssessmentResult } from "../../shared/models/assessment-result";
import { ScaleResult } from "../../shared/models/scale-result";
import { Subscription } from 'rxjs';
import { DacConfig } from 'app/shared/services/dac-config.service';
import { ModalService } from 'app/shared/services/modal.service';
import { PrintDialogComponent } from '../print-dialog/print-dialog.component';
import { AssessmentGraderService } from 'app/shared/services/assessment-grader.service';
import { AssessmentResultHydrator } from 'app/shared/services/assessment-result-hydrator.service';
import { ScaleResultsTableSummaryComponent } from '../scale-results-table-summary/scale-results-table-summary.component';
import { TherapistScaleResultsComponent } from '../report-items/therapist-scale-results/therapist-scale-results.component';

@Component({
  selector: 'app-assessment-result-view',
  templateUrl: './assessment-result-view.component.html',
  styleUrls: ['./assessment-result-view.component.css']
})
export class AssessmentResultViewComponent implements OnInit, AfterViewInit {

    private queryParamsSubscription: Subscription;
    private _result:AssessmentResult;
    private _clientId:string;

    public hasLoadedResults:boolean;

    @ViewChild("tableSummary") public scaleResultsTableSummaryComponent:ScaleResultsTableSummaryComponent;

    @ViewChild("therapistScaleResults") public scaleResultsComponent:TherapistScaleResultsComponent;

  /**
   * Whether to show therapist note information in the report.  Otherwise it's hidden so it can be
   * given to a client or print off a shorter report.
   */
  public displayTherapistNotes:boolean;

  /**
   * Whether to show the print dialog or not.
   */
  public showPrintDialog:boolean;

  public allowPrinting:boolean;

  private _visibleScaleResults:ScaleResult[];

  private rescoreResults:boolean;


    constructor(
      private route:ActivatedRoute
      ,private router:Router
		  ,private _assessmentService:AssessmentService
      ,private _grader:AssessmentGraderService
      ,private _resultHydrator:AssessmentResultHydrator
      ,private location:Location
      ,private elementRef:ElementRef
      ,private dacConfig:DacConfig
      ,private modalService:ModalService) {
        this._visibleScaleResults = [];
        this.allowPrinting = this.dacConfig.isFeatureEnabled('pdf.printing');
    }

  showCommandButtons() {
      return !this.dacConfig.seamlessMode;
  }

  ngOnInit() {
    this.queryParamsSubscription = this.route.queryParams.subscribe(params => {
      if (params['notes'] && params['notes'] == '1') {
        this.displayTherapistNotes = true;
      }
      else {
        this.displayTherapistNotes = false;
      }
      if (params['rescore'] && params['rescore'] == '1') {
        this.rescoreResults = true;
      } else {
        this.rescoreResults = false;
      }
    });



    // TODO: stephen do we need the rx switchTo piece?
    this.route.params.subscribe(async (params) => {
          this._clientId = params['id'];
          let resultId = params['resultId'];
          this.hasLoadedResults = false;
          try {

            let result;
            if (this.rescoreResults) {
              let latestResult = await this._assessmentService.getAssessmentResultWithLatestAssessment(this._clientId, resultId);
              result = this._grader.grade(latestResult.assessment); // regrade with our latest assessment
              console.log("Regraded score is ", result);
            } else {
              result = await this._assessmentService.getAssessmentResult(this._clientId, resultId);
            }

            this.hasLoadedResults = true;
            this.result = result;

            if (this.result == null || !this.result.scaleResults || this.result.scaleResults.length < 1) {
              // this.goBack();
            } else {
              this._visibleScaleResults = this.result.scaleResults.filter(sr => sr.scale && sr.scale.includeInReports === true);
            }
          } catch(error) {
            console.error(error);
            if (error && error.status === 404 || error.status === 400) {
              // this.goBack();
            }
          };
        });
    }

    public generatePdfHtml() {
      let rootNode = this.elementRef.nativeElement as HTMLElement;
      let itemsContainer = rootNode.querySelector(".itemsContainer");

      let textArea = rootNode.querySelector(".dc-assignment-textarea");
      if (itemsContainer && textArea) {
        let docFragment = new DocumentFragment();
        docFragment.append(itemsContainer.cloneNode(true));
        let clonedAppScaleResultsTableSummary = docFragment.querySelector("app-scale-results-table-summary");
        let clonedTherapistScales = docFragment.querySelector("app-therapist-scale-results");
        clonedAppScaleResultsTableSummary.replaceWith(this.scaleResultsTableSummaryComponent.getPrintContent());
        clonedTherapistScales.replaceWith(this.scaleResultsComponent.getPrintContent());
        let node = docFragment.querySelector('.itemsContainer') as HTMLElement;
        textArea.textContent = node.innerHTML;
      }
    }

    public get allowRescoreResults()
    {
      return  this.dacConfig.isFeatureEnabled("assessments.reports.saveresult");
    }

    public saveRescoredResult()
    {
      console.log("Saving result ", this.result);
      this._assessmentService.clientId = this._clientId;
      this._assessmentService.saveAssessmentResult(this.result);
    }

    ngAfterViewInit() {
      // we need to let outside listeners like the server pdf generator know we have finished loading so we can generate the report
      console.log("adding dac-report-loaded class");
      document.body.classList.add("dac-report-loaded");

    }

    ngAfterContentInit() {
    }

    public get scaleResults () {
      return this._visibleScaleResults;
    }


    public goBack() {
      this.location.back();
  }

    public get clientID() : string {
        return this._clientId;
    }

    public get result() : AssessmentResult {
		return this._result;
	}

	public set result(v:AssessmentResult) {
		this._result = v;
  }

  public browserPrint() {
    if (window && window.print) {
      window.print();
    }
  }

  public print() {
    this.modalService.openDialog(PrintDialogComponent);
  }

}
