import { Injectable } from "@angular/core";

@Injectable({
  providedIn: 'root',
})
export class ThemeService  {
  private _isMainContentFluid;
  private _isStickyFixContainerEnabled;

  constructor() {
    this._isMainContentFluid = false;
  }

  public isMainContentFluid() {
    return this._isMainContentFluid;
  }

  public setMainContentFluid(isFluid:boolean) {
    this._isMainContentFluid = isFluid === true;
  }

  public setStickyFixContainerEnabled(stickyFixEnabled:boolean) {
    this._isStickyFixContainerEnabled = stickyFixEnabled;
  }

  public isStickyFixContainerEnabled() {
    return this._isStickyFixContainerEnabled;
  }
}
