import { Injectable } from '@angular/core';



import {DacConfig} from "../../shared/services/dac-config.service";
import { HTTPService } from '../../shared/services/http.service';

import {IReport} from "../models/i-report";
import { Assignment } from 'app/shared/models/Assignment';
import { AssignedAssessment } from 'app/shared/models/AssignedAssessment';
import { AssignedAssessmentGroup } from 'app/shared/models/AssignedAssessmentGroup';
import { AssignedQuestionnaire } from 'app/shared/models/AssignedQuestionnaire';

@Injectable({
  providedIn: 'root',
})
export class ReportService {

  private reports:any[];
  private allReportsIncludingHidden:boolean;


  constructor(private dacConfig:DacConfig, private http:HTTPService) {
    this.reports = null;
  }

  private getReportObjects(showAllReports:boolean = false) : Promise<IReport[]> {
    if (this.reports && this.allReportsIncludingHidden == showAllReports) {

      return Promise.resolve(this.reports);
    }

    return this.http.get<IReport[]>("assessment-reports", {showAllReports:showAllReports})
      .then((reports) => {
        this.reports = reports;
        this.allReportsIncludingHidden = showAllReports;
        return this.reports;
      })
      .catch((error) => {
        console.error(error);
        return [];
      });
  }
  list(showAllReports:boolean = false) : Promise<any[]> {
    return this.getReportObjects(showAllReports);
  }

  get(reportId:string) : Promise<any> {
    return this.http.get<IReport>("assessment-reports/" + reportId)
      .then((report) => {
        return report;
      })
      .catch(error => {
        console.error(error);
        throw Error("Could not find report with id " + reportId);
      });
  }

  create(item:IReport) {
    return this.http.compressedPost<IReport>("assessment-reports", item)
    .then((result) => {
      this.reports = null; // invalidate the cache
      return result;
    });
  }

  save(item:IReport) {
    return this.http.compressedPost<IReport>("assessment-reports/" + item.id, item)
    .then((result) => {
      this.reports = null; // invalidate the cache
      return item;
    });
  }


  getReportRequirements() : Promise<{id:string, name: string, requirements: string[]}[]> {
    return this.getReportObjects()
      .then(reports => reports.map((r) => {
        let linkedAssessments = r.linkedAssessments || [];
        return {id: r.id, name: r.name, requirements: linkedAssessments};
      }));
  }

  getReportsForAssignment(assignment:Assignment) : Promise<{id:string, name:string, requirements:string[]}[]>{
    if (!assignment) {
      return Promise.resolve([]);
    }
    return this.getReportObjects().then(reports => {
      let assignmentReports = [];
      if (assignment.Type == "Assessment") {
        let assessmentAssignment = assignment.Items[0] as AssignedAssessment;
        assignmentReports = reports.filter(r => r.linkedAssessments && r.linkedAssessments.length == 1 && r.linkedAssessments[0] == assessmentAssignment.Uid);
      }
      else if (assignment.Type == "AssessmentGroup") {
        let group = assignment as AssignedAssessmentGroup;
        assignmentReports = reports.filter(r => r.linkedGroup && r.linkedGroup.id == group.AssessmentGroupId);
      }
      else if (assignment.Type == 'LibraryAsset') {
        assignmentReports = []; // no reports for now.
      } else if (assignment.Type == 'Questionnaire' || assignment.Type == "ExternalQuestionnaire" || assignment.Type == "TemplateProfile") {
        let questionnaireAssignment = assignment.Items[0] as AssignedQuestionnaire;
        assignmentReports = reports.filter(r => r.linkedQuestionnaires && r.linkedQuestionnaires.length == 1 && r.linkedQuestionnaires[0] == questionnaireAssignment.QuestionnaireId);
      }
      else {
        throw new Error("Unsupported assignment type");
      }
      return assignmentReports;
    });
  }

}
