import { AfterViewInit, ChangeDetectorRef, Component, Input, OnInit, ViewChild } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { AssessmentResult } from "app/shared/models/assessment-result";
import { AlertService } from "app/shared/services/alert.service";
import { DacConfig } from "app/shared/services/dac-config.service";
import { ModalService } from "app/shared/services/modal.service";
import { QuestionnaireService } from "app/shared/services/questionnaire.service";
import { TherapistScaleResultsComponent } from "../report-items/therapist-scale-results/therapist-scale-results.component";
import { Question } from "app/shared/models/question";
import { TextboxQuestion } from "app/shared/models/textbox-question";
import { PrintDialogComponent } from "../print-dialog/print-dialog.component";
import { Location } from "@angular/common";

