import { AssignmentItem } from "./AssignmentItem";

export class AssignedQuestionnaire extends AssignmentItem
{
    private questionnaireId:string|null;
    private documentTemplateId:number|null;
    private documentId:number|null;

    public constructor() {
        super();
        this.Type = "Questionnaire";
    }

    public get QuestionnaireId() {
        return this.questionnaireId;
    }

    public set QuestionnaireId(v:string) {
        this.questionnaireId = v;
    }

    public get DocumentTemplateId() {
        return this.documentTemplateId;
    }

    public set DocumentTemplateId(v:number|null) {
        this.documentTemplateId = v;
    }

    public get DocumentId() {
        return this.documentId;
    }

    public set DocumentId(v:number|null) {
        this.documentId = v;
    }

}
