import {Role} from '../models/role';
import {AuthService} from '../../login/services/auth.service';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges, AfterViewInit, ElementRef, ViewChild } from '@angular/core';
import { User} from "../../shared/models/user";
import { UserService } from "../../shared/services/user.service";
import { MatSelectChange } from '@angular/material/select';

@Component({
  selector: 'user-selector',
  templateUrl: './user-selector.component.html',
  styleUrls: ['./user-selector.component.css']
})
export class UserSelectorComponent implements OnInit, OnChanges  {
  @Output() selected = new EventEmitter<User>();
  @Input() allowEmpty:boolean;
  @Input() user:User;
  @Input() filter:(user:User) => boolean;

  public selectedUser:User;
  public selectOptions:{label: string, value: string, selected: boolean}[];
  private users:User[];
  private _selectedUserId:string;

  constructor(private userService:UserService, private authService:AuthService) {
    this.users = [];
  }

  ngOnInit() {
    this.authService.getCurrentUser().then(currentUser => {

      let getUsers = Promise.resolve([currentUser]);
      if (currentUser.role <= Role.Admin) {
        getUsers = this.userService.getUsers();
      }

      getUsers.then((users) => {
        let activeUsers = users.filter(u => u.enabled); // only display activated users.
        if (this.filter) {
          this.users = activeUsers.filter(this.filter);
        } else {
          this.users = activeUsers;
        }

        this.setupSelectedUser();
      });
    });

  }
  ngAfterContentInit() {

  }
  ngOnChanges() {
    this.setupSelectedUser();
  }

  private setupSelectedUser() {
    if (!this.user) {
      this.selectedUser = null;
    } else {
      this.selectedUser = this.users.find(u => u.id == this.user.id);
    }
  }

  public get Users() {
    return this.users;
  }

  public get selectedUserId() {
    return this._selectedUserId;
  }

  public set selectedUserId(v:string) {
    this._selectedUserId = v;
  }

  public selectUser(selected:MatSelectChange) {
    let user = selected.value;
    this.selected.emit(user);
  }
}
