import { LibraryExerciseAnswer } from "./LibraryExerciseAnswer";

export class LibraryAsset {
    id: number;
    uuid: string;
    type: "article"|"assignment";
    originalCreator:string;
    title: string;
    description: string;
    content: string;
    journal: string;
    creationDate: Date
    lastUpdateDate: Date;
    tags:string[];

    constructor() {
      this.type = "assignment";
      this.creationDate = new Date();
      this.lastUpdateDate = new Date();
      this.tags = [];
    }

    public getAnswerIdPrefix() {
        return 'answer-id-';
    }

    public hasTag(tag:string) {
        return this.tags && this.tags.indexOf(tag) >= 0;
    }

    public titleContainsString(str:string) {
        if (!this.title || this.title.trim() =="") {
            return false;
        }
        return this.title.toLowerCase().indexOf(str.toLowerCase()) >= 0;
    }
}
