if (window['dacAppConfig']) {
  let dacAppConfig = window['dacAppConfig'] || {};
  if (dacAppConfig.clientId) {
    environment.smartConfig.client_id = dacAppConfig.clientId;
  }
  if (dacAppConfig['fhirUrl']) {
    environment.smartConfig.fhirUrl = dacAppConfig['fhirUrl'];
  }
  if (dacAppConfig['apiUrl']) {
    environment.apiURL = dacAppConfig['apiUrl'];
  }
  if (dacAppConfig['smartStyleUrl']) {
    environment.smartConfig.smartStyleUrl = dacAppConfig['smartStyleUrl'];
  }
  if (dacAppConfig['logoSrc']) {
    environment.applicationLogo = dacAppConfig['logoSrc'];
  }
}
