import { ToastData, TOAST_CONFIG_TOKEN, ToastConfig, defaultToastConfig, ToastType } from './toast-config';
import { AnimationEvent } from '@angular/animations';
import { Component, Inject } from '@angular/core';
import { toastAnimations, ToastAnimationState } from './toast-animation';
import { ToastRef } from './toast-ref';

@Component({
  selector: 'dac-toast',
  templateUrl: './toast.component.html',
  animations: [toastAnimations.fadeToast],
})
export class ToastComponent {
  animationState: ToastAnimationState = 'default';
  iconClass: string;
  type:ToastType;

  private intervalId: null | ReturnType<typeof setTimeout>;
  private toastConfig:ToastConfig;

  constructor(
    readonly data: ToastData,
    readonly ref: ToastRef
    // @Inject(TOAST_CONFIG_TOKEN) private toastConfig: ToastConfig
    ) {
      this.toastConfig = defaultToastConfig;
      this.type = data.type;
      switch (data.type) {
        case 'success':
          this.iconClass = 'check';
        break;
        case 'danger':
        case 'warning':
          this.iconClass = 'exclamation-circle';
        break;
        case 'info':
          this.iconClass = 'info-circle';
        break;
      }
  }
  ngOnInit() {
    let delay = this.data.timeToDisplay || this.toastConfig.timeToDisplay || 5000;
    this.intervalId = setTimeout(() => {
      this.close();
    }, delay);
  }

  ngOnDestroy() {
    clearTimeout(this.intervalId);
  }

  close() {
    this.ref.close();
  }

  onFadeFinished(event: AnimationEvent) {
    const { toState } = event;
    const isFadeOut = (toState as ToastAnimationState) === 'closing';
    const itFinished = this.animationState === 'closing';

    if (isFadeOut && itFinished) {
      this.close();
    }
  }

  public get Config() : ToastConfig {
    return this.toastConfig;
  }
}
