import { Injectable } from '@angular/core';
import {Company } from "../models/company";
import { User } from "../models/user";
// TODO: stephen only used for mocking the service right now...
import {UserService} from "./user.service";
import { HTTPService } from 'app/shared/services/http.service';

class CompanyUserStatistic {
    role_name:string;
    role_id:number;
    username:string;
    id:number;
    client_count:number;
}

@Injectable()
export class CompanyService {

    constructor(private userService:UserService, private http:HTTPService) {
    }

    getCompany(id:number) {
        return this.http.get<Company>("/admin/companies/" + id + ".json").then((data) => {
            return this.hydrateCompanyFromData(data);
        });
    }

    getCompanies() : Promise<Company[]> {
        return this.http.get<{results:Company[]}>("/admin/companies", {limit: 1000}).then((data) => {
            if (!(data.results && data.results.length)) {
                return [];
            }
            return data.results.map(r => this.hydrateCompanyFromData(r));
        });
    }

    getUserStatistics(companyId:number) : Promise<CompanyUserStatistic[]> {
        return this.http.get<CompanyUserStatistic[]>("/admin/companies/" + companyId + "/users").then((data) => {
            return data;
        });
    }

    getOrderStatistics(companyId:number) : Promise<{orders:number, failedOrders:number, revenueAverage:number, revenue:number}[]> {
        return this.http.get<{orders:number, failedOrders:number, revenueAverage:number, revenue:number}[]>("/admin/companies/" + companyId + "/orders").then((data) => {
            return data;
        });
    }

    createCompany(company:Company) : Promise<Company> {
        // TODO: stephen need to handle the new user password
      return this.http.post<Company>("/admin/companies", company).then((data) => {
        return this.hydrateCompanyFromData(data);
      });
    }

    registerNewCompany(company:Company, authToken:string) : Promise<Company>{
      // TODO: stephen need to handle the new user password
      return this.http.post<Company>("/companies", {company: company, authToken:authToken}).then((data) => {
        return this.hydrateCompanyFromData(data);
      });
    }

    saveAccountOwnerCompany(company:Company) {

        return this.http.post<Company>("/companies/" + company.id + ".json", company).then((data) => {
            return this.hydrateCompanyFromData(data);
        });
    }

    getAccountOwnerCompany(id:number) {
        return this.http.get<Company>("/companies/" + id + ".json").then((data) => {
            return this.hydrateCompanyFromData(data);
        });
    }

    private hydrateCompanyFromData(record:any) {
        let company = new Company();
        Object.assign(company, record);
        company.email = record._primaryContactEmail;
        let user = new User();
        Object.assign(user, record._user);
        company.user = user;
        return company;
    }
}
