@NgModule({
  imports: [
    CommonModule
    ,FormsModule
    ,RouterModule
    ,HttpClientModule
    ,MatSelectModule
    ,ToastModule
  ],
  declarations: [AssessmentPickerComponent, SpinIconComponent, IfFeatureEnabled, ConditionEditComponent
      ,SearchInputComponent, BackButtonComponent, UserSelectorComponent, RoleSelectorComponent
      , UsernameCheckerComponent, PdfGenerateBtnComponent,
      BuyItemButtonComponent
      ,BillingDetailsComponent,
      BillingCardComponent,
      CopyClipboardComponent,
      AssessmentGroupListComponent,
      TabsComponent,
      TabComponent,
      CardComponent
    ],

  // TODO: stephen just found out according to this documentation that all of my lazy loaded modules
  // create distinct copies of these services I'm sharing...  So I need to figure out what to do with
  // all the providers I want to be singletons...
  // https://angular.io/guide/ngmodule#!#shared-module
  providers: [],
  exports: [
    AssessmentPickerComponent, SpinIconComponent, IfFeatureEnabled, ConditionEditComponent
    , SearchInputComponent, BackButtonComponent, UserSelectorComponent
    , RoleSelectorComponent, UsernameCheckerComponent, BuyItemButtonComponent
    , BillingDetailsComponent, BillingCardComponent, AssessmentGroupListComponent
    ,CommonModule
    ,FormsModule
    ,PdfGenerateBtnComponent
    ,CopyClipboardComponent
    ,TabsComponent
    ,TabComponent
    ,CardComponent
  ]
})
export class SharedModule {

  static forRoot(): ModuleWithProviders<SharedModule> {
    return {
      ngModule: SharedModule,
      providers: [windowProvider,
        Authenticator, DacConfig, PaginatedResultsLoaderFactory, AssessmentService, AssessmentResultHydrator
        , AssessmentHydrator,HTTPService, UniqueIdService, ClientService, StripePaymentProcessor
        , FhirService
        , AssessmentTokenService, AssessmentCompressorService, QuestionFactoryService, AssessmentGraderService
        , QuestionAnswerPopulator, UserService, CompanyService, AlertService, Pako, PdfService, HostSiteService
        , SsoCrossDomainMessenger, ShoppingService, OrderService, BillingService,AssignmentHydratorService
        , AssignmentService, CopyClipboardService, AssessmentGroupService, SubscriptionHydratorService, MetricsService
        , QuestionnaireService, FhirSmartStyleService ]
    };
  }
}
