import { Component, Input, OnInit } from "@angular/core"
import { ActivatedRoute } from "@angular/router";
import { AssessmentResult } from "app/shared/models/assessment-result"
import { Scale } from "app/shared/models/scale";
import { ScaleRange } from "app/shared/models/scale-range";
import { ScaleResult } from "app/shared/models/scale-result";
import { AlertService } from "app/shared/services/alert.service";
import { QuestionnaireService } from "app/shared/services/questionnaire.service";

