import { AssignmentItem } from "./AssignmentItem";

export class AssignedLibraryAsset extends AssignmentItem
{
    private assetId:number|null;
    private assetUuid:string|null;

    public constructor() {
        super();
    }


    public get AssetUuid() : string {
      return this.assetUuid;
    }


    public set AssetUuid(v : string) {
      this.assetUuid = v;
    }


    public get AssetId() {
        return this.assetId;
    }

    public set AssetId(v:number) {
        this.assetId = v;
    }

}
