import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { DacConfig } from 'app/shared/services/dac-config.service';

@Component({
  selector: 'dac-pricing-table',
  templateUrl: './pricing-table.component.html',
  styleUrls: ['./pricing-table.component.scss']
})
export class PricingTableComponent implements OnInit {

  @Output() selected = new EventEmitter<string>();
  public freeTrialPeriod;

  constructor(dacConfig:DacConfig) {
    this.freeTrialPeriod = dacConfig.freeTrialPeriod;
  }

  ngOnInit() {
  }

  selectPlan(type:string) {
    this.selected.next(type);
  }


}
