import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { PageNotFoundComponent } from "./theme/page-not-found/page-not-found.component";
import { AuthGuard } from './login/services/auth-guard.service';
import { AuthGuardLogin } from './login/services/auth-guard-login.service';
import { AppComponent } from './app.component';
import { StandaloneContainerComponent } from './theme/standalone-container/standalone-container.component';
import { AdminGuard } from './login/services/admin-guard.service';
import { AdminLoginComponent } from './login/admin-login/admin-login.component';
import { AdminLoginFinalizeComponent } from './login/admin-login-finalize/admin-login-finalize.component';
import { AdminLoginPageComponent } from './theme/admin-login-page/admin-login-page.component';
import { AdminLoginFinalizePageComponent } from './theme/admin-login-finalize-page/admin-login-finalize-page.component';

const routes: Routes = [
  { path: '',
    redirectTo: '/login'
    ,pathMatch: 'full'
    // component: PageNotFoundComponent, // never used as our AuthGuardLogin will redirect based on the SMART app configuration
    // this is a dummy guard that we just use to redirect based on login state to admin or assessments
  },
  // { path: 'help', redirectTo: '/home/help', pathMatch: 'full' },
  // { path: 'baa', redirectTo: '/home/baa', pathMatch: 'full' },
  // { path: 'privacy', redirectTo: '/home/privacy', pathMatch: 'full' },
  // { path: 'terms-and-conditions', redirectTo: '/home/terms-and-conditions', pathMatch: 'full' },
  // { path: 'home', 'loadChildren': () => import('./home/home.module').then(m => m.HomeModule)},
  { path: 'ehr', data: {'modulePath': '/ehr'}, 'loadChildren': () => import('./ehr/ehr.module').then(m => m.EhrModule)},
  { path: 'std', component: StandaloneContainerComponent, children: [
    { path: 'admin',canActivate: [AdminGuard], data: {'modulePath': '/std/admin'}, 'loadChildren': () => import('./admin/admin.module').then(m => m.AdminModule)},
    { path: 'assessments', canActivate: [AuthGuardLogin], data: {'modulePath': '/std/assessments'}, 'loadChildren': () => import('./assessments/assessments.module').then(m => m.AssessmentsModule)},
  ]}
  ,{ path: 'login', component: AdminLoginPageComponent}
  ,{ path: 'loginFinalize', component: AdminLoginFinalizePageComponent}
  ,{ path: 'logout', redirectTo: '/login', pathMatch: 'full' }
  ,{ path: '**', component: PageNotFoundComponent }
];

@NgModule({
  imports: [
    CommonModule
    ,RouterModule.forRoot(routes, {enableTracing: false})
  ],
  exports: [ RouterModule ],
  declarations: []
})
export class AppRoutingModule { }
