export class AssessmentResultAnswer {

  private _question_id:string;
  private  _answer:any;
  private _score:number;

  constructor() {
    this._score = 0;
    this._question_id = null;
    this._answer = null;
  }


  public get question_id() : string {
    return this._question_id;
  }


  public set question_id(v : string) {
    this._question_id = v;
  }


  public get score() : number {
    return this._score;
  }


  public set score(v : number) {
    this._score = v;
  }


  public get answer() : any {
    return this._answer;
  }


  public set answer(v : any) {
    this._answer = v;
  }
}
