@Injectable({
  providedIn: 'root',
})
export class FhirSmartStyleService {
  private smartStyles:SmartStyleSettings;
  constructor(private dacConfig:DacConfig) {

  }

  public async loadSmartStyles() {
    if (this.dacConfig.smartConfigStyleUrl) {
      try {
        let response = await fetch(this.dacConfig.smartConfigStyleUrl);
        let jsonStyles = await response.json();
        if (jsonStyles) {
          this.smartStyles = jsonStyles;
          this.dacConfig.applicationLogoUrl = jsonStyles.logo_primary;
          this.dacConfig.smartStyles = jsonStyles;
        }
      }
      catch (error) {
        console.error(error);
      }
    }
  }
}
