import {Assessment} from "../../shared/models/assessment";

export class AssessmentGroup {
    private id: number;
    private name: string;
    private profileId: string|null;
    private assessments:Assessment[];
    private created:Date;
    private updated:Date;

    constructor() {
        this.assessments = [];
        this.created = new Date();
        this.updated = new Date();
    }

    get ProfileId() {
      return this.profileId;
    }
    set ProfileId(id:string|null) {
      this.profileId = id;
    }
    get Id() {
        return this.id;
    }
    set Id(v:number) {
        this.id = v;
    }

    get Name() {
        return this.name;
    }
    set Name(v:string) {
        this.name = v;
    }

    get Assessments() {
        return this.assessments;
    }
    set Assessments(v:Assessment[]) {
        this.assessments = v;
    }

    get Created() {
        return this.created;
    }
    set Created(v) {
        this.created = v;
    }
    get Updated() {
        return this.updated;
    }
    set Updated(v) {
        this.updated = v;
    }




}
