import { Assignment } from "./Assignment";
import { AssignedAssessment } from "./AssignedAssessment";
import { AssignmentItem } from "./AssignmentItem";

export class AssignedTemplateProfile extends Assignment
{
    private profileId:string|null;
    private name:string;

    public constructor() {
        super();
        this.Type = "TemplateProfile";
    }

    public get ProfileId() : string {
        return this.profileId;
    }
    public set ProfileId(v:string) {
        this.profileId = v;
    }

    public getQuestionnaireItems() : AssignmentItem[] {
        return this.Items.filter(i => i.Type == "Questionnaire" || i.Type == "ExternalQuestionnaire")
                .map(i => i as AssignedAssessment);
    }

    public get Name(): string {
        return this.name;
    }
    public set Name(v: string) {
        this.name = v;
    }

    public get isGroup() {
      return true;
    }
}
