	private addPendingDefer(defer, resultID) {
		if (!this._pendingResults[resultID]) {
			this._pendingResults[resultID] = [];
		}
		this._pendingResults[resultID].push(defer);
	}

	getLatestClientAssessmentResult(clientId:string, assessmentUID:string) {
		var requestData = {
				clientID: clientId
				,assessmentUID: assessmentUID
				,resultID: null
			};
		return new Promise((resolve, reject) => {
			this._dac$http.get<AssessmentResult>("assessment-results", requestData)
				.then((data) => {
					var result:AssessmentResult = this.createAssessmentResultFromJSON(data);
					this._results[result.id] = result;
					resolve(result);
					return result;
				}, (error) => {
					reject(error);
					return error;
				});
		});
	}

  async getAssessmentResultWithLatestAssessment(clientId, resultID) {
    var requestData:any = {
      clientID: clientId,
      resultID: resultID
    };
    try {
      let resultData = await this._dac$http.get<{_assessment:any}>("assessment-results", requestData);
      let resultDataAssessment = resultData._assessment || {_uid:""};
      let uid = resultDataAssessment._uid || "";

      var assessmentVersionData = {
        clientID: clientId
      };
      let assessmentData = await this._dac$http.get<Assessment>("assessments/" + uid, requestData);

      resultData._assessment = assessmentData;
      return this._resultHydrator.hydrate(resultData);
    }
    catch (error) {
      console.error(error);
      throw error;
    }
  }

  getAssessmentResultList(clientId, resultIds:string[]) : Promise<AssessmentResult[]> {
    let requestData:any = {
      clientID: clientId,
      "resultIds[]": resultIds // need to handle php cases when working with arrays
    };
    return this._dac$http.get<AssessmentResult[]>("assessment-results", requestData)
			.then((data) => {
        let results = [];
        if (data && data.length) {
          results = data.map(r => this.createAssessmentResultFromJSON(r));
        }

				return results;
			});
  }

