import { Assignment } from "../models/Assignment";
import { AssignedAssessmentGroup } from "../models/AssignedAssessmentGroup";
import { AssignedAssessment } from "../models/AssignedAssessment";
import { Injectable } from "@angular/core";
import { AssignedLibraryAsset } from "../models/AssignedLibraryAsset";
import { AssignmentSingle } from "../models/AssignmentSingle";
import { AssignedQuestionnaire } from "../models/AssignedQuestionnaire";
import { AssignedTemplateProfile } from "../models/AssignedTemplateProfile";
import { AssignmentItem } from "../models/AssignmentItem";

@Injectable()
export class AssignmentHydratorService
{

	public hydrateAssignment(record:any) : Assignment
	{
		let assignment:Assignment;
		if (record.type == "AssessmentGroup") {
			assignment = Object.assign(new AssignedAssessmentGroup(), record) as AssignedAssessmentGroup;
      assignment.Items = record.items.map(i => this.hydrateAssignmentItem(i));
		}
    else if (record.type == 'TemplateProfile') {
      assignment = Object.assign(new AssignedTemplateProfile(), record) as AssignedTemplateProfile;
      assignment.Items = record.items.map(i => this.hydrateAssignmentItem(i));
    }
		else {
			assignment = Object.assign(new AssignmentSingle(), record);
			let recordItem = record.items && record.items.length ? record.items[0] : {};
			let item = this.hydrateAssignmentItem(recordItem);
			assignment.Items = [item];
		}
		return assignment;
	}

  private hydrateAssignmentItem(recordItem:any) : AssignmentItem {
    let item;
			if (recordItem.type == "Assessment")
			{
				item = Object.assign(new AssignedAssessment(), recordItem);
			}
			else if (recordItem.type == "LibraryAsset")
			{
				item = Object.assign(new AssignedLibraryAsset(), recordItem);
			}
      else if (recordItem.type == "Questionnaire")
      {
        item = Object.assign(new AssignedQuestionnaire(), recordItem);
      }
			else {
				console.error("Failed to find valid assignment ", recordItem);
				item = Object.assign(new AssignedAssessment(), recordItem); // default it to an assessment
			}
      return item;
  }
}
