import { Component, OnInit, Input, OnDestroy, ChangeDetectorRef } from '@angular/core';
import {AuthService} from "../services/auth.service";
import {Router, ActivatedRoute} from "@angular/router";
import { AlertService } from 'app/shared/services/alert.service';
import { Subscription } from 'rxjs';
import { DacConfig } from 'app/shared/services/dac-config.service';
// import { AmplifyAuthService } from '../services/amplify-auth-service';
import { AuthState } from '../models/AuthState';
import { CognitoUserInterface } from '../models/CognitoUserInterface';
import { FhirService } from 'app/shared/services/fhir-service';

@Component({
  selector: 'app-admin-login',
  templateUrl: './admin-login.component.html',
  styleUrls: ['./admin-login.component.scss']
})
export class AdminLoginComponent implements OnInit,OnDestroy {

  private returnUrl: string = '';
  private _user:CognitoUserInterface;
  private _authState:string;
  private _authSubscription:Subscription;

  @Input() disableRegistration:boolean;

  constructor(
    private router:Router
    ,private route:ActivatedRoute
    ,private authService:AuthService
    ,private fhirService:FhirService
  ) {
    // TODO: @adunsulag how to handle the refresh token

    this._authSubscription = this.authService.authStateChange$().subscribe(authState => {
      console.log("Auth state is ", authState);
      this._authState = authState.state;
      if (this._authState === AuthState.SIGNED_IN) {
        // either going back to the original requested url or going to the dashboard.
        this.router.navigateByUrl(this.returnUrl);
      }
      this._user = authState.user;
    });
  }

  get user() : CognitoUserInterface {
    return this._user;
  }

  ngOnInit() {
    let loginType = this.route.snapshot.queryParams['loginType'] || 1;
    // TODO: @adunsulag if we are already authorized we want to skip this step.
    if (loginType == 1) {
      this.fhirService.authorizeUserAdmin('/loginFinalize');
    } else {
      this.fhirService.authorizeClient('/loginFinalize');
    }
    // this.route.queryParams
    //   .subscribe(params => this.returnUrl = params['return'] || '/admin/');
  }

  ngOnDestroy() {
    if (this._authSubscription) {
      this._authSubscription.unsubscribe();
    }
  }

  public get authState() {
    return this._authState;
  }

  public isForgotPassword() {
    return this._authState == AuthState.FORGOT_PASSWORD;
  }

  public isRequireNewPassword() {
    return this._authState == AuthState.REQUIRE_NEW_PASSWORD;
  }

  public isSignIn() {
    return this._authState == AuthState.SIGN_IN;
  }

  public isConfirmSignIn() {
    return this._authState == AuthState.CONFIRM_SIGN_IN;
  }
}
